/**
 * Application Bootstrap
 * Initializes all services, registers routes, and starts the application
 */

// Import services
import CategoryMappingService from './services/CategoryMappingService.js';
import ExamProgressService from './services/ExamProgressService.js';
import IHKContentService from './services/IHKContentService.js';
import ModuleService from './services/ModuleService.js';
import ProgressService from './services/ProgressService.js';
import QuizService from './services/QuizService.js';
import Router from './services/Router.js';
import SpecializationService from './services/SpecializationService.js';
import StateManager from './services/StateManager.js';
import StorageService from './services/StorageService.js';
import ThemeManager from './services/ThemeManager.js';

// Import utilities
import accessibilityHelper from './utils/AccessibilityHelper.js';

// Import error handling and UI components
import ErrorBoundary from './components/ErrorBoundary.js';
import toastNotification from './components/ToastNotification.js';

// Import views
import Navigation from './components/Navigation.js';
import HomeView from './components/HomeView.js';
import ModuleListView from './components/ModuleListView.js';
import ModuleDetailView from './components/ModuleDetailView.js';
import IHKQuizListView from './components/IHKQuizListView.js';
import IHKQuizView from './components/IHKQuizView.js';
import ProgressView from './components/ProgressView.js';
import WheelView from './components/WheelView.js';
import NotFoundView from './components/NotFoundView.js';
import SpecializationSelector from './components/SpecializationSelector.js';

// Note: Using IHK quiz components for all quizzes (superior UI/UX)

// Import sample data
import modulesData from './data/modules.json';

/**
 * Main Application Class
 */
class App {
  constructor() {
    this.services = {};
    this.navigation = null;
  }

  /**
   * Initialize the application
   */
  async init() {
    try {
      // Initialize accessibility helper
      accessibilityHelper.init();

      // Set up global error handling
      this.setupErrorHandling();

      // Initialize core services
      this.initializeServices();

      // Load initial data
      await this.loadInitialData();

      // Initialize theme
      this.initializeTheme();

      // Initialize specialization selection (must be before navigation)
      await this.initializeSpecializationSelection();

      // Create navigation
      this.createNavigation();

      // Register routes
      this.registerRoutes();

      // Set up global event listeners
      this.setupGlobalEventListeners();

      // Start router
      this.services.router.init();

      // Announce app ready to screen readers
      accessibilityHelper.announce('Application loaded successfully');
    } catch (error) {
      console.error('❌ Failed to initialize application:', error);
      this.showFatalError(error);
    }
  }

  /**
   * Initialize all services
   */
  initializeServices() {
    // Get app container
    const appContainer = document.getElementById('app');
    if (!appContainer) {
      throw new Error('App container not found');
    }

    // Create main content container for views
    const mainContent = document.createElement('main');
    mainContent.id = 'main-content';
    mainContent.className = 'main-content';
    appContainer.appendChild(mainContent);

    // Storage service
    this.services.storageService = new StorageService();

    // State manager
    this.services.stateManager = new StateManager(this.services.storageService);

    // Specialization service (initialize early for content filtering)
    this.services.specializationService = new SpecializationService(
      this.services.stateManager,
      this.services.storageService
    );

    // Category mapping service (initialize after specialization service)
    this.services.categoryMappingService = new CategoryMappingService(
      this.services.specializationService
    );

    // Theme manager
    this.services.themeManager = new ThemeManager(this.services.storageService);

    // Router (pass main content container)
    this.services.router = new Router(mainContent);

    // Connect specialization service to router for context-aware routing
    this.services.router.setSpecializationService(
      this.services.specializationService
    );

    // IHK Content service (initialize before ModuleService with specialization and category mapping support)
    this.services.ihkContentService = new IHKContentService(
      this.services.stateManager,
      this.services.storageService,
      this.services.specializationService,
      this.services.categoryMappingService
    );

    // Module service (now includes IHK modules with specialization filtering)
    this.services.moduleService = new ModuleService(
      this.services.stateManager,
      this.services.storageService,
      this.services.ihkContentService,
      this.services.specializationService
    );

    // Quiz service (now includes IHK quizzes with specialization filtering)
    this.services.quizService = new QuizService(
      this.services.stateManager,
      this.services.storageService,
      this.services.ihkContentService,
      this.services.specializationService
    );

    // Progress service
    this.services.progressService = new ProgressService(
      this.services.stateManager,
      this.services.storageService,
      this.services.moduleService,
      this.services.quizService
    );

    // Exam Progress service
    this.services.examProgressService = new ExamProgressService(
      this.services.stateManager,
      this.services.storageService
    );
  }

  /**
   * Load initial data from storage or use defaults
   */
  async loadInitialData() {
    // Try to load state from storage
    this.services.stateManager.loadFromStorage();

    // Check if we have modules data
    let modules = this.services.stateManager.getState('modules');
    if (!modules || modules.length === 0) {
      // Load default modules
      this.services.stateManager.setState('modules', modulesData);
    }

    // Note: Quizzes are now loaded from IHK system via IHKContentService
    // No need to initialize quiz data here

    // Initialize progress if not exists
    let progress = this.services.stateManager.getState('progress');
    if (!progress) {
      this.services.stateManager.setState('progress', {
        modulesCompleted: [],
        modulesInProgress: [],
        quizAttempts: [],
        lastActivity: new Date().toISOString(),
      });
    }

    // Migrate existing users to have a default specialization
    this.services.specializationService.migrateExistingUser(
      'anwendungsentwicklung'
    );

    // Save initial state
    this.services.stateManager.saveToStorage();
  }

  /**
   * Initialize theme based on saved preference or system default
   */
  initializeTheme() {
    // Initialize the theme manager first
    this.services.themeManager.init();

    // Start watching for system preference changes
    this.services.themeManager.watchSystemPreference();
  }

  /**
   * Initialize specialization selection for first-time users
   */
  async initializeSpecializationSelection() {
    try {
      // Always create specialization selector (needed for navigation)
      this.specializationSelector = new SpecializationSelector(
        this.services.specializationService,
        specializationId => {
          // Handle specialization selection
          this.handleSpecializationSelection(specializationId);
        }
      );

      // Initialize the selector (this will show the modal for first-time users)
      await this.specializationSelector.init();
    } catch (error) {
      console.error('Error initializing specialization selection:', error);
    }
  }

  /**
   * Handle specialization selection from the selector
   */
  handleSpecializationSelection(specializationId) {
    try {
      // Update navigation if it exists
      if (this.navigation) {
        this.navigation.updateSpecialization(specializationId);
      }

      // Refresh current view to show relevant content
      if (this.services.router) {
        this.services.router.refresh();
      }

      // Save state
      this.services.stateManager.saveToStorage();

      console.warn(`Specialization selected: ${specializationId}`);
    } catch (error) {
      console.error('Error handling specialization selection:', error);
    }
  }

  /**
   * Create navigation component
   */
  createNavigation() {
    this.navigation = new Navigation(
      this.services.themeManager,
      this.services.router,
      this.services.specializationService,
      this.specializationSelector
    );
    const appContainer = document.getElementById('app');
    const mainContent = document.getElementById('main-content');
    const navElement = this.navigation.render();

    // Insert navigation before main content
    appContainer.insertBefore(navElement, mainContent);
  }

  /**
   * Register all application routes
   */
  registerRoutes() {
    const router = this.services.router;

    // Wrap all routes with error boundary
    // Home route
    router.register(
      '/',
      ErrorBoundary.wrap(async () => {
        const view = new HomeView(this.services);
        return await view.render();
      })
    );

    // Modules list route
    router.register(
      '/modules',
      ErrorBoundary.wrap(async () => {
        const view = new ModuleListView(this.services);
        return await view.render();
      })
    );

    // Module detail route
    router.register(
      '/modules/:id',
      ErrorBoundary.wrap(async params => {
        const view = new ModuleDetailView(this.services, params);
        return await view.render();
      })
    );

    // Quizzes list route (using IHK component for all quizzes)
    router.register(
      '/quizzes',
      ErrorBoundary.wrap(async () => {
        const view = new IHKQuizListView(this.services);
        return await view.render();
      })
    );

    // Quiz detail route (using IHK component for all quizzes)
    router.register(
      '/quizzes/:id',
      ErrorBoundary.wrap(async params => {
        const view = new IHKQuizView(this.services);
        return await view.render(params.id);
      })
    );

    // Debug assessment demo route (non-production)
    router.register(
      '/assessment-demo',
      ErrorBoundary.wrap(async () => {
        const main = document.getElementById('main-content');
        // Lazy import to avoid bundle cost in production
        const { default: MicroQuizPanel } = await import(
          './assessment/components/MicroQuizPanel.js'
        );
        const demoContainer = document.createElement('div');
        demoContainer.id = 'assessment-demo-container';
        main.appendChild(demoContainer);
        const panel = new MicroQuizPanel(demoContainer);
        // Create a minimal quiz fixture
        const fixture = {
          id: 'demo-quiz-1',
          title: 'Demo Micro-Quiz',
          questions: [
            {
              id: 'q1',
              type: 'single-choice',
              question: 'Pick A',
              options: ['A', 'B'],
              correctAnswer: 'A',
            },
          ],
        };
        panel.render(fixture);
        return demoContainer;
      })
    );

    // Progress route
    router.register(
      '/progress',
      ErrorBoundary.wrap(async () => {
        const view = new ProgressView(this.services);
        return await view.render();
      })
    );

    // Wheel route - random module selector
    router.register(
      '/wheel',
      ErrorBoundary.wrap(async () => {
        const view = new WheelView(this.services);
        return await view.render();
      })
    );

    // Note: IHK content is now seamlessly integrated into the regular modules and quizzes views
    // No separate IHK routes needed - all content is accessible through /modules and /quizzes

    // Register 404 handler
    router.registerNotFound(() => {
      const view = new NotFoundView(this.services);
      return view.render();
    });
  }

  /**
   * Set up global event listeners
   */
  setupGlobalEventListeners() {
    // Listen for specialization modal requests from navigation
    document.addEventListener('showSpecializationModal', () => {
      if (this.specializationSelector) {
        this.specializationSelector.showSpecializationModal(false);
      }
    });

    // Listen for specialization changes to update navigation and refresh content
    document.addEventListener('specialization-changed', event => {
      if (this.navigation) {
        this.navigation._updateSpecializationIndicator();
      }

      // Refresh current view to show filtered content
      if (this.services.router) {
        this.services.router.refresh();
      }
    });
  }

  /**
   * Set up global error handling
   */
  setupErrorHandling() {
    // Handle uncaught errors
    window.addEventListener('error', event => {
      console.error('Uncaught error:', event.error);
      toastNotification.error(
        'An unexpected error occurred. Please refresh the page.'
      );
      event.preventDefault();
    });

    // Handle unhandled promise rejections
    window.addEventListener('unhandledrejection', event => {
      console.error('Unhandled promise rejection:', event.reason);
      toastNotification.error(
        'An unexpected error occurred. Please try again or refresh the page.'
      );
      event.preventDefault();
    });

    // Handle storage quota errors globally
    window.addEventListener('storage', _event => {
      if (_event.key === null) {
        // Storage was cleared
        toastNotification.warning(
          'Storage was cleared. Your progress may be lost.'
        );
      }
    });

    // Handle storage quota warnings
    window.addEventListener('storage-quota-warning', _event => {
      toastNotification.warning(
        'Storage is almost full. Only essential progress was saved.',
        {
          duration: 10000,
          action: {
            label: 'Export Progress',
            onClick: () => {
              window.location.hash = '#/progress';
            },
          },
        }
      );
    });

    // Handle storage quota errors
    window.addEventListener('storage-quota-error', event => {
      toastNotification.handleStorageError(event.detail.error);
    });
  }

  /**
   * Show fatal error screen
   */
  showFatalError(error) {
    const appContainer = document.getElementById('app');
    appContainer.innerHTML = `
      <div style="
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        min-height: 100vh;
        padding: 2rem;
        text-align: center;
      ">
        <h1 style="font-size: 2rem; margin-bottom: 1rem; color: #ef4444;">
          ⚠️ Application Error
        </h1>
        <p style="margin-bottom: 2rem; color: #6b7280;">
          We're sorry, but something went wrong. Please try refreshing the page.
        </p>
        <button 
          onclick="window.location.reload()" 
          style="
            padding: 0.75rem 1.5rem;
            background: #3b82f6;
            color: white;
            border: none;
            border-radius: 0.5rem;
            cursor: pointer;
            font-size: 1rem;
          "
        >
          Refresh Page
        </button>
        <details style="margin-top: 2rem; text-align: left; max-width: 600px;">
          <summary style="cursor: pointer; color: #6b7280;">Error Details</summary>
          <pre style="
            margin-top: 1rem;
            padding: 1rem;
            background: #f3f4f6;
            border-radius: 0.5rem;
            overflow: auto;
            font-size: 0.875rem;
          ">${error.stack || error.message}</pre>
        </details>
      </div>
    `;
  }
}

// Initialize and start the application
const app = new App();
app.init();

export default app;
